<template>
  <el-card class="main-card">
    <div class="title">{{ this.$route.name }}</div>
    <!--  -->
    <div class="operation-container">
      <el-button
        type="primary"
        size="small"
        icon="el-icon-plus"
        @click="openModel(null)"
      >
        
      </el-button>
      <el-button
        type="danger"
        size="small"
        icon="el-icon-deleteItem"
        :disabled="linkIdList.length == 0"
        @click="deleteFlag = true"
      >
        
      </el-button>
      <!--  -->
      <div style="margin-left:auto">
        <el-input
          v-model="keywords"
          prefix-icon="el-icon-search"
          size="small"
          placeholder=""
          style="width:200px"
          @keyup.enter.native="listLinks"
        />
        <el-button
          type="primary"
          size="small"
          icon="el-icon-search"
          style="margin-left:1rem"
          @click="listLinks"
        >
          
        </el-button>
      </div>
    </div>
    <!--  -->
    <el-table
      border
      :data="linkList"
      @selection-change="selectionChange"
      v-loading="loading"
    >
      <!--  -->
      <el-table-column type="selection" width="55" />
      <el-table-column
        prop="linkAvatar"
        label=""
        align="center"
        width="180"
      >
        <template slot-scope="scope">
          <img :src="scope.row.linkAvatar" width="40" height="40" />
        </template>
      </el-table-column>
      <el-table-column prop="linkName" label="" align="center" />
      <el-table-column prop="linkAddress" label="" align="center" />
      <el-table-column prop="linkIntro" label="" align="center" />
      <el-table-column
        prop="createTime"
        label=""
        width="140"
        align="center"
      >
        <template slot-scope="scope">
          <i class="el-icon-time" style="margin-right:5px" />
          {{ scope.row.createTime | date }}
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column label="" align="center" width="160">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="openModel(scope.row)">
            
          </el-button>
          <el-popconfirm
            title=""
            style="margin-left:1rem"
            @onConfirm="deleteLink(scope.row.id)"
          >
            <el-button size="mini" type="danger" slot="reference">
              
            </el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="pagination-container"
      background
      @size-change="sizeChange"
      @current-change="currentChange"
      :current-page="current"
      :page-size="size"
      :total="count"
      :page-sizes="[10, 20]"
      layout="total, sizes, prev, pager, next, jumper"
    />
    <!--  -->
    <el-dialog :visible.sync="deleteFlag" width="30%">
      <div class="dialog-title-container" slot="title">
        <i class="el-icon-warning" style="color:#ff9900" />
      </div>
      <div style="font-size:1rem"></div>
      <div slot="footer">
        <el-button @click="deleteFlag = false"> </el-button>
        <el-button type="primary" @click="deleteLink(null)">
           
        </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog :visible.sync="addOrEdit" width="30%">
      <div class="dialog-title-container" slot="title" ref="linkTitle" />
      <el-form label-width="80px" size="medium" :model="linkForm">
        <el-form-item label="">
          <el-input style="width:250px" v-model="linkForm.linkName" />
        </el-form-item>
        <el-form-item label="">
          <el-input style="width:250px" v-model="linkForm.linkAvatar" />
        </el-form-item>
        <el-form-item label="">
          <el-input style="width:250px" v-model="linkForm.linkIntro" />
        </el-form-item>
        <el-form-item label="">
          <el-input style="width:250px" v-model="linkForm.linkAddress" />
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="addOrEdit = false"> </el-button>
        <el-button type="primary" @click="addOrEditCategory">
           
        </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  created() {
    this.listLinks();
  },
  data: function() {
    return {
      loading: true,
      deleteFlag: false,
      addOrEdit: false,
      linkIdList: [],
      linkList: [],
      linkForm: {
        id: null,
        linkName: "",
        linkAvatar: "",
        linkIntro: "",
        linkAddress: ""
      },
      keywords: null,
      current: 1,
      size: 10,
      count: 0
    };
  },
  methods: {
    selectionChange(linkList) {
      this.linkIdList = [];
      linkList.forEach(item => {
        this.linkIdList.push(item.id);
      });
    },
    sizeChange(size) {
      this.size = size;
      this.listLinks();
    },
    currentChange(current) {
      this.current = current;
      this.listLinks();
    },
    deleteLink(id) {
      var param = {};
      if (id == null) {
        param = { data: this.linkIdList };
      } else {
        param = { data: [id] };
      }
      this.axios.delete("/api/admin/links", param).then(({ data }) => {
        if (data.flag) {
          this.$notify.success({
            title: "",
            message: data.message
          });
          this.listLinks();
        } else {
          this.$notify.error({
            title: "",
            message: data.message
          });
        }
        this.deleteFlag = false;
      });
    },
    openModel(link) {
      if (link != null) {
        this.linkForm = JSON.parse(JSON.stringify(link));
        this.$refs.linkTitle.innerHTML = "";
      } else {
        this.linkForm.id = null;
        this.linkForm.linkName = "";
        this.linkForm.linkAvatar = "";
        this.linkForm.linkIntro = "";
        this.linkForm.linkAddress = "";
        this.$refs.linkTitle.innerHTML = "";
      }
      this.addOrEdit = true;
    },
    addOrEditCategory() {
      if (this.linkForm.linkName.trim() == "") {
        this.$message.error("");
        return false;
      }
      if (this.linkForm.linkAvatar.trim() == "") {
        this.$message.error("");
        return false;
      }
      if (this.linkForm.linkIntro.trim() == "") {
        this.$message.error("");
        return false;
      }
      if (this.linkForm.linkAddress.trim() == "") {
        this.$message.error("");
        return false;
      }
      this.axios.post("/api/admin/links", this.linkForm).then(({ data }) => {
        if (data.flag) {
          this.$notify.success({
            title: "",
            message: data.message
          });
          this.listLinks();
        } else {
          this.$notify.error({
            title: "",
            message: data.message
          });
        }
        this.addOrEdit = false;
      });
    },
    listLinks() {
      this.axios
        .get("/api/admin/links", {
          params: {
            current: this.current,
            size: this.size,
            keywords: this.keywords
          }
        })
        .then(({ data }) => {
          this.linkList = data.data.recordList;
          this.count = data.data.count;
          this.loading = false;
        });
    }
  }
};
</script>
