<template>
  <el-card class="main-card">
    <div class="title">{{ this.$route.name }}</div>
    <!--  -->
    <div class="operation-container">
      <el-button
        v-if="isDelete == 0"
        type="danger"
        size="small"
        icon="el-icon-deleteItem"
        :disabled="articleIdList.length == 0"
        @click="updateIsDelete = true"
      >
        
      </el-button>
      <el-button
        v-else
        type="danger"
        size="small"
        icon="el-icon-deleteItem"
        :disabled="articleIdList.length == 0"
        @click="remove = true"
      >
        
      </el-button>
      <!--  -->
      <div style="margin-left:auto">
        <el-select
          v-model="condition"
          placeholder=""
          size="small"
          style="margin-right:1rem"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
        <el-input
          v-model="keywords"
          prefix-icon="el-icon-search"
          size="small"
          placeholder=""
          style="width:200px"
          @keyup.enter.native="listArticles"
        />
        <el-button
          type="primary"
          size="small"
          icon="el-icon-search"
          style="margin-left:1rem"
          @click="listArticles"
        >
          
        </el-button>
      </div>
    </div>
    <!--  -->
    <el-table
      border
      :data="articleList"
      @selection-change="selectionChange"
      v-loading="loading"
    >
      <!--  -->
      <el-table-column type="selection" width="55" />
      <el-table-column prop="articleTitle" label="" align="center" />
      <!--  -->
      <el-table-column
        prop="categoryName"
        label=""
        width="120"
        align="center"
      />
      <!--  -->
      <el-table-column
        prop="tagDTOList"
        label=""
        width="180"
        align="center"
      >
        <template slot-scope="scope">
          <el-tag
            v-for="item of scope.row.tagDTOList"
            :key="item.tagId"
            style="margin-right:0.2rem;margin-top:0.2rem"
          >
            {{ item.tagName }}
          </el-tag>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column
        prop="viewsCount"
        label=""
        width="80"
        align="center"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.viewsCount">
            {{ scope.row.viewsCount }}
          </span>
          <span v-else>0</span>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column
        prop="likeCount"
        label=""
        width="80"
        align="center"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.likeCount">
            {{ scope.row.likeCount }}
          </span>
          <span v-else>0</span>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column
        prop="createTime"
        label=""
        width="140"
        align="center"
      >
        <template slot-scope="scope">
          <i class="el-icon-time" style="margin-right:5px" />
          {{ scope.row.createTime | date }}
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column
        prop="updateTime"
        label=""
        width="140"
        align="center"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.updateTime">
            <i class="el-icon-time" style="margin-right:5px" />
            {{ scope.row.updateTime | date }}
          </span>
          <span v-else></span>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="isTop" label="" width="100" align="center">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.isTop"
            active-color="#13ce66"
            inactive-color="#F4F4F5"
            :disabled="scope.row.isDelete == 1 || scope.row.isDraft == 1"
            :active-value="1"
            :inactive-value="0"
            @change="changeTop(scope.row)"
          />
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column label="" align="center" width="160">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            @click="editArticle(scope.row.id)"
            v-if="scope.row.isDelete == 0"
          >
            
          </el-button>
          <el-popconfirm
            title=""
            style="margin-left:10px"
            @onConfirm="updateArticleStatus(scope.row.id)"
            v-if="scope.row.isDelete == 0"
          >
            <el-button size="mini" type="danger" slot="reference">
              
            </el-button>
          </el-popconfirm>
          <el-popconfirm
            title=""
            v-if="scope.row.isDelete == 1"
            @onConfirm="updateArticleStatus(scope.row.id)"
          >
            <el-button size="mini" type="success" slot="reference">
              
            </el-button>
          </el-popconfirm>
          <el-popconfirm
            style="margin-left:10px"
            v-if="scope.row.isDelete == 1"
            title=""
            @onConfirm="deleteArticles(scope.row.id)"
          >
            <el-button size="mini" type="danger" slot="reference">
              
            </el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="pagination-container"
      background
      @size-change="sizeChange"
      @current-change="currentChange"
      :current-page="current"
      :page-size="size"
      :total="count"
      :page-sizes="[10, 20]"
      layout="total, sizes, prev, pager, next, jumper"
    />
    <!--  -->
    <el-dialog :visible.sync="updateIsDelete" width="30%">
      <div class="dialog-title-container" slot="title">
        <i class="el-icon-warning" style="color:#ff9900" />
      </div>
      <div style="font-size:1rem"></div>
      <div slot="footer">
        <el-button @click="updateIsDelete = false"> </el-button>
        <el-button type="primary" @click="updateArticleStatus(null)">
           
        </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog :visible.sync="remove" width="30%">
      <div class="dialog-title-container" slot="title">
        <i class="el-icon-warning" style="color:#ff9900" />
      </div>
      <div style="font-size:1rem"></div>
      <div slot="footer">
        <el-button @click="remove = false"> </el-button>
        <el-button type="primary" @click="deleteArticles(null)">
           
        </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  created() {
    this.listArticles();
  },
  data: function() {
    return {
      loading: true,
      updateIsDelete: false,
      remove: false,
      options: [
        {
          value: '{"isDelete":0,"isDraft":0}',
          label: ""
        },
        {
          value: '{"isDelete":1,"isDraft":null}',
          label: ""
        },
        {
          value: '{"isDelete":0,"isDraft":1}',
          label: ""
        }
      ],
      condition: '{"isDelete":0,"isDraft":0}',
      articleList: [],
      articleIdList: [],
      keywords: null,
      isDelete: 0,
      isDraft: 0,
      current: 1,
      size: 10,
      count: 0
    };
  },
  methods: {
    selectionChange(articleList) {
      this.articleIdList = [];
      articleList.forEach(item => {
        this.articleIdList.push(item.id);
      });
    },
    editArticle(id) {
      this.$router.push({ path: "/articles/" + id });
    },
    updateArticleStatus(id) {
      let param = new URLSearchParams();
      if (id != null) {
        param.append("idList", [id]);
      } else {
        param.append("idList", this.articleIdList);
      }
      param.append("isDelete", this.isDelete == 0 ? 1 : 0);
      this.axios.put("/api/admin/articles", param).then(({ data }) => {
        if (data.flag) {
          this.$notify.success({
            title: "",
            message: data.message
          });
          this.listArticles();
        } else {
          this.$notify.error({
            title: "",
            message: data.message
          });
        }
        this.updateIsDelete = false;
      });
    },
    deleteArticles(id) {
      var param = {};
      if (id == null) {
        param = { data: this.articleIdList };
      } else {
        param = { data: [id] };
      }
      this.axios.delete("/api/admin/articles", param).then(({ data }) => {
        if (data.flag) {
          this.$notify.success({
            title: "",
            message: data.message
          });
          this.listArticles();
        } else {
          this.$notify.error({
            title: "",
            message: data.message
          });
        }
        this.remove = false;
      });
    },
    sizeChange(size) {
      this.size = size;
      this.listArticles();
    },
    currentChange(current) {
      this.current = current;
      this.listArticles();
    },
    changeTop(article) {
      let param = new URLSearchParams();
      param.append("isTop", article.isTop);
      this.axios.put("/api/admin/articles/top/" + article.id, param);
    },
    listArticles() {
      this.axios
        .get("/api/admin/articles", {
          params: {
            current: this.current,
            size: this.size,
            keywords: this.keywords,
            isDelete: this.isDelete,
            isDraft: this.isDraft
          }
        })
        .then(({ data }) => {
          this.articleList = data.data.recordList;
          this.count = data.data.count;
          this.loading = false;
        });
    }
  },
  watch: {
    condition() {
      const condition = JSON.parse(this.condition);
      this.isDelete = condition.isDelete;
      this.isDraft = condition.isDraft;
      this.listArticles();
    }
  }
};
</script>
